.App {
  text-align: center;
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.black-nav{
  background: black;
  width: 100%;
  display: flex;
  color: white;
  padding: 20px;
  font-weight: 600;
  font-size: 20px;
  box-sizing: border-box;
}

.nav-button{
  background: Black;
  color:White;
  width: 90px;
  height: 30px;
  border:white;
  border-style: solid;
  border-radius: 15%;
  border-width: 2px;
  float:left;
  margin: 5px;
}

.chat-name{
  background: black;
  color: white;
  width: 150px;
  height: 30px;
  border-radius: 5%;
  margin: 5px;
  text-align: center;
}

.speech-bubble {
	position: relative;
	background: #f5e429;
	border-radius: .4em;
}

.speech-bubble:after {
	content: '';
	position: absolute;
	left: 0;
	top: 50%;
	width: 0;
	height: 0;
	border: 8px solid transparent;
	border-right-color: #f5e429;
	border-left: 0;
	border-bottom: 0;
	margin-top: -4px;
	margin-left: -8px;
}